// import config from './config';

export default (resourceName = '') => {
  // const { port, protocol, host } = config;

  const host = '192.168.31.214';
  const protocol = 'http';
  const port = 3001;

  const resourcePath = resourceName;

  if (!host) {
    return `/${resourcePath}`;
  }

  return `${protocol}://${host}:${port}/${resourcePath}`;
};
